


































import { Component, Prop, Watch, Mixins } from 'vue-property-decorator'
import Validates from '@utils/mixins/Validates'
import { FormBus } from '@components/form/FormProvider.vue'

export interface OnChangeParameters {
    value: string
}

interface Validate {
    (value: string): boolean
}

interface Timer {
    count: number
    unit: 'minute' | 'second'
    format?: string
}

/**
 * TODO:
 * 2. label에 표시될 tooltip이 아직 공통 컴포넌트가 없음, 해야함
 * 3. 단위 표시할 텍스트 노출방식에 대한 논의가 필요함
 */

@Component
export default class ButtonField extends Mixins(Validates) {
    /**
     * @category Refs
     */
    $refs!: Vue['$refs'] & {
        input: HTMLInputElement
    }
    /**
     * @category PROPS
     */

    /** form에 사용될 id */
    @Prop({ type: String, required: true })
    readonly id!: string

    /** label태그에 들어갈 텍스트 */
    @Prop({ type: String, default: '', required: true })
    readonly label!: string

    /** label을 비노출여부 */
    @Prop(Boolean)
    readonly hiddenLabel!: boolean

    /** 최대 자릿수 지정 */
    @Prop({ type: Number, default: 9999 })
    readonly maxlength!: number

    /** 입력 전 표시될 가이드 텍스트 */
    @Prop({ type: String, default: '' })
    readonly placeholder!: string

    /** 읽기전용 여부 */
    @Prop({ type: Boolean, default: false })
    readonly readonly!: boolean

    /** 비활성화 여부 */
    @Prop({ type: Boolean, default: false })
    readonly disabled!: boolean

    /** 기본 값 */
    @Prop(String)
    readonly defaultValue!: string

    /** 버튼에 들어갈 텍스트 */
    @Prop({ type: String, required: true })
    readonly buttonText!: string

    /** 유형 */
    @Prop({ type: Boolean, default: false })
    readonly cert!: boolean

    /** 타이머 */
    @Prop({ type: Object, default: () => ({ count: 3, unit: 'minute' }) })
    private timer!: Timer

    /**
     * @category DATA(State)
     */

    /** 실제 값 */
    private value: string = this.defaultValue || ''

    /** focus 상태 */
    private focusedClass: boolean = false

    /**
     * @title Watch
     */

    @Watch('value')
    changeValue(newValue: string) {
        /**
         * value가 변경 될때 마다 호출되는 callback(value: string, index: number, maxLength: number)
         */
        this.$emit('change', {
            value: newValue,
            fieldName: this._.camelCase(this.id),
        })

        FormBus.$emit('form:update', {
            value: newValue,
            fieldName: this._.camelCase(this.id),
        })
    }

    /**
     * @category METHOD
     * @title Custom Methods
     */

    toggleFocus() {
        this.focusedClass = !this.focusedClass
    }

    onSearch() {
        /**
         * keydown 이벤트
         * @event search
         */
        this.$emit('search', this.value)
    }

    onInput(event: InputEvent) {
        this.value = this.$refs.input.value.replace(/\,/g, '')
    }

    onKeydownEnter(event: KeyboardEvent) {
        this.onSearch()
    }

    onFocus(event: FocusEvent) {
        /**
         * focus 이벤트
         * @event focus
         */
        this.$emit('focus', event)

        this.toggleFocus()
    }

    onBlur(event: FocusEvent) {
        this.toggleFocus()
    }

    clearValue() {
        this.value = ''
        this.$refs.input.focus()
    }

    /**
     * @category METHODS
     * @title Lifecycle
     */
    mounted() {
        /**
         * mounted 이벤트
         * @event mounted
         */
        this.$emit('mounted', this.$refs.input)
    }
}
