











import { Component, Prop, Vue } from 'vue-property-decorator'

@Component
export default class Dimmed extends Vue {
    /**
     * @category Props
     */

    /** 노출 여부 */
    @Prop({ type: Boolean, default: false, required: true })
    readonly show!: boolean

    /**
     * @category Methods
     */

    onClose() {
        /**
         * click 이벤트 방출
         * @event click
         */
        this.$emit('click')
    }
}
