













import { Component, Prop, Vue } from 'vue-property-decorator'

export interface TitleProps {
    title?: string
    subTitle?: string
}

@Component
export default class Title extends Vue {
    /**
     * @category Prop
     */

    /** 페이지 제목 */
    @Prop({ type: String })
    readonly title!: string

    /** 제목하단 텍스트 / 페이지 설명 텍스트 */
    @Prop({ type: String })
    readonly subText!: string
}
